//*-------------------------*/
//* forLoop
//*-------------------------*/

.box{
  height: 15rem;
  width: 15rem;
  float: left;
  background-color: #ccc;
}
// 1--to 
@for $i from 1 to 5{
  .box-#{$i}{
    background-color:darken(white , $i * 10%);
  }
}
// 2--through 
@for $i from 1 through 5{
  .box-#{$i}{
    background-color:darken(white , $i * 10%);
  }
}
